const btcToUsd = (value: string | BigNumber): string => {
  return new BigNumber(value).div(new BigNumber(10).exponentiatedBy(8)).times(8909.76).toFixed(0)
}

const App = () => {

  const [previousAmount, setPreviousAmount] = React.useState("0");
  const [nextAmount, setNextAmount] = React.useState("0");
  const [message, setMessage] = React.useState("");
  const [error, setError] = React.useState("");
  const [bitcoinAddress, setBitcoinAddress] = React.useState("");
  const gatewayJS = React.useMemo(() => new GatewayJS("testnet"), []);
  let [web3, setWeb3] = React.useState<Web3 | null>(null);
  let [levels, setLevels] = React.useState<OrderedMap<string, Level> | null>(null);

  const updateLevels = async () => {
    if (!web3) {
      return;
    }
    const contract = new web3.eth.Contract(ABI.abi as unknown as AbiItem[], contractAddress) as FreeZBTC;
    const nextAmountRead = await contract.methods.nextAmount().call();
    setNextAmount(nextAmountRead.toString());

    const previousAmountRead = await contract.methods.previousAmount().call();
    setPreviousAmount(previousAmountRead.toString());

    const events = await web3.eth.getPastLogs({
      address: contractAddress,
      fromBlock: contractBlock,
      toBlock: "latest",
      topics: [web3.utils.sha3("Deposit(uint256,bytes,bytes)")],
    });

    levels = levels || OrderedMap<string, Level>();

    for (const event of events) {
      const [, rawAmount] = event.topics;
      const amount = new BigNumber(rawAmount, 16).toFixed();
      let { 0: playerRaw } = web3.eth.abi.decodeParameters(["bytes", "bytes"], event.data);
      if (playerRaw.slice(0, 2) === "0x") {
        playerRaw = playerRaw.slice(2);
      }
      const player = Buffer.from(playerRaw, "hex").toString();
      levels = levels.set(amount, { amount, player });
    }

    levels = levels.sortBy(i => new BigNumber(i.amount).toNumber());

    const lastLevel: Level | undefined = levels.last();
    let lastAmount = lastLevel ? new BigNumber(lastLevel.amount) : new BigNumber(22500 / 2);

    lastAmount = lastAmount.times(2);

    while (lastAmount.lt(2100000000000000)) {
      levels = levels.set(lastAmount.toString(), {
        amount: lastAmount.toString(),
        player: undefined,
      });

      lastAmount = lastAmount.times(2);
    }

    setLevels(levels);
  }

  const logError = (error: Error | string) => {
    console.error(error);
    setError(String(((error || {}) as { message?: string }).message || error));
  }

  const log = (message: string) => {
    setMessage(message);
  }

  const earn = async () => {
    if (!web3) {
      return;
    }

    try {
      await gatewayJS.open({
        // Send BTC from the Bitcoin blockchain to the Ethereum blockchain.
        sendToken: GatewayJS.Tokens.BTC.Btc2Eth,

        // Amount of BTC we are sending (in Satoshis)
        suggestedAmount: new BigNumber(nextAmount).times(1.001).toFixed(0), // Convert to Satoshis

        // The contract we want to interact with
        sendTo: contractAddress,

        // The name of the function we want to call
        contractFn: "earn",

        // Arguments expected for calling `earn`
        contractParams: [
          {
            name: "_bitcoinAddress",
            type: "bytes",
            value: web3.utils.fromAscii(bitcoinAddress),
          }
        ],

        // confirmationless: true,
      }).result();

      await updateLevels();
    } catch (error) {
      // Handle error
      console.error(error);
    }
  }

  const recoverTrades = async (gatewayJS: GatewayJS) => {
    // Re-open incomplete trades
    const previousGateways = await gatewayJS.getGateways();
    for (const trade of Array.from(previousGateways.values())) {
      if (trade.status === ShiftInStatus.ConfirmedOnEthereum || trade.status === ShiftOutStatus.ReturnedFromRenVM) { continue; }
      const gateway = gatewayJS.open(trade);
      gateway.pause();
      gateway.result()
        .on("status", (status) => console.log(`[GOT STATUS] ${status}`))
        .then(console.log)
        .catch(console.error);
    }
  }

  React.useEffect(() => {

    (async () => {
      let web3Provider;

      // Initialize web3 (https://medium.com/coinmonks/web3-js-ethereum-javascript-api-72f7b22e2f0a)
      // Modern dApp browsers...
      if (window.ethereum) {
        web3Provider = window.ethereum;
        try {
          // Request account access
          await window.ethereum.enable();
        } catch (error) {
          // User denied account access...
          logError("Please allow access to your Web3 wallet.");
          return;
        }
      }
      // Legacy dApp browsers...
      else if (window.web3) {
        web3Provider = window.web3.currentProvider;
      }
      // If no injected web3 instance is detected, fall back to Ganache
      else {
        logError("Please install MetaMask!");
        return;
      }

      web3 = new Web3(web3Provider as any);

      const networkID = await web3.eth.net.getId();
      if (networkID !== 42) {
        logError("Please set your network to Kovan.");
        return;
      }

      setWeb3(web3);

      recoverTrades(gatewayJS).catch(console.error);

      updateLevels();
      setInterval(() => {
        updateLevels();
      }, 10 * 1000);
    })().catch(console.error);
  }, []);

  const onChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setBitcoinAddress(event.target.value);
  }

  const toReadable = React.useCallback((amount: string | BigNumber) => new BigNumber(amount).div(new BigNumber(10).exponentiatedBy(8)).toFixed(), []);

  const readableNextAmount = React.useMemo(() =>
    new BigNumber(nextAmount).times(1.001).decimalPlaces(0).div(new BigNumber(10).exponentiatedBy(8)).toFixed(),
    // new BigNumber(nextAmount).div(new BigNumber(10).exponentiatedBy(8)).toFixed(),
    [nextAmount],
  );

  const readableEarning = React.useMemo(() =>
    new BigNumber(nextAmount).div(new BigNumber(10).exponentiatedBy(8)).times(2).toFixed(),
    [nextAmount],
  );

  useEffect(() => {
    document.title = emojis[nextAmount] ? `${emojis[nextAmount]} Free zBTC ${emojis[nextAmount]}` : "Free zBTC";
  }, [nextAmount]);

  return (
    <div className="App">
      <div className="levels">
        {levels !== null ? levels.map(level => {

          if (level.amount === nextAmount) {
            return <div key={level.amount} className={`level level-current`}>
              <div className="emoji">{emojis[level.amount]}</div>
              <div className="right">
                <p>Deposit {readableNextAmount} BTC (${btcToUsd(new BigNumber(nextAmount).times(1.001).decimalPlaces(0))}) to get back {readableEarning} BTC from the next player</p>
                <p className="inputs">
                  <input onChange={onChange} value={bitcoinAddress} placeholder="Testnet BTC address" />
                  <button disabled={bitcoinAddress === ""} onClick={() => earn().catch(logError)}>Deposit {readableNextAmount} TESTNET BTC</button>
                </p>
                <p>{message}</p>
                {error ? <p style={{ color: "red" }}>{error}</p> : null}
              </div>
            </div>
          }

          if (level.amount === previousAmount) {
            return <div key={level.amount} className={`level ${level.player ? "level-redeemed" : "level-empty"}`}>
              <span className="emoji">{emojis[level.amount]}</span>
              <div className="right">
                <p className="yellow">{level.player && level.player?.slice(0, 6)} about to receive {toReadable(level.amount)} BTC (${btcToUsd(level.amount)})</p>
              </div>
            </div>
          }

          return <div key={level.amount} className={`level ${level.player ? "level-redeemed" : "level-empty"}`}>
            <span className="emoji">{emojis[level.amount]}</span>
            <div className="right">
              {level.player ?
                <p className="green">{level.player.slice(0, 6)} received {toReadable(level.amount)} BTC (${btcToUsd(level.amount)})!</p> :
                <p>{toReadable(level.amount)} BTC (${btcToUsd(level.amount)})</p>
              }
            </div>
          </div>
        }).valueSeq() : <><p>Loading...</p>{error ? <p style={{ color: "red" }}>{error}</p> : null}</>}
      </div>
    </div>
  );
}

export default App;